.vertical-center {
  display: flex;
  align-items: center;
}

.horizontal-center {
  display: flex;
  justify-content: center;
}

.groups {
  display: flex;
  flex-direction: column;
}

.group {
  margin-bottom: 0.5em;
  width: 95vw;
}

.group__info {
  height: 125px;
  flex-direction: row;
  font-size: 0.8em;
}

.shadow {
  filter: drop-shadow(0 0 3px rgba(0,0,0,0.5));
}

.icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: auto 100px;
}
